.pager {
    width: 80%;
    margin: 20px auto;
    text-align: center;
    font-size: 18px;

    span {
        color: rgb(97, 101, 155);
        margin: 0 10px;
        padding: 3px 5px;
        cursor: pointer;

        &.disabled {
            color: rgb(201, 204, 211);
            cursor: not-allowed;
        }

        &.active {
            color: #f40;
        }
    }
}