@import "@/common.less";

.container {
    width: 75%;
    margin: 30px auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    text-align: center;
    row-gap: 10px;

    .item {
        padding: 10px;
        display: flex;
        flex-direction: column;
        font-size: 12px;

        img {
            width: 172px;
            height: 254px;
            object-fit: cover;
        }

        .title {
            margin: 15px 0;
            font-size: 16px;
        }

        .rate {
            color: @fontColor;

            i {
                margin-left: 10px;
            }
        }
    }
}